<template>
  <div>
    <teleport to="body">
      <div class="modal fade" id="findPasswordModal" tabindex="-1" aria-labelledby="exampleModalLabel"
           aria-hidden="true">
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h1 class="modal-title fs-5" id="exampleModalLabel">비밀번호 찾기</h1>
              <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
            </div>
            <div class="modal-body">
              <div class="card p-4">
                <div v-if="isAuthNumberCorrect === false">
                  <!-- Main form for ID and Phone Number -->
                  <form @submit.prevent="searchIdMember">
                    <div class="mb-3">
                      <label for="searchId" class="form-label">아이디</label>
                      <input type="text" id="searchId" class="form-control" v-model="id" :readonly="isIdReadOnly">
                    </div>
                    <div class="mb-3">
                      <label for="searchTel2" class="form-label">휴대폰번호</label>
                      <input type="text" id="searchTel2" class="form-control" v-model="tel" :readonly="isTelReadOnly"
                             placeholder="ex. 010-3749-4868">
                    </div>
                    <div>
                      <button type="button" class="btn btn-primary" @click="startTimer" v-if="showResend"
                              style="width: 100%">인증문자
                        재요청
                      </button>
                      <button type="submit" class="btn btn-primary" @click="resendAuth" style="width: 100%" v-else>인증 문자
                        요청
                      </button>
                    </div>
                  </form>
               <div v-if="showResend">
                      <div class="mb-3">
                        <label for="authNumber" class="form-label">인증번호</label>
                        <input type="text" id="authNumber" class="form-control" v-model="authNumber"
                               @change="checkAuthNumber" :placeholder="'인증번호 6자리 입력 ' + timeLeft">
                      
                        <button type="button" class="btn btn-primary" style="width: 100%">확인</button>
                      </div>
                  </div>
                </div>
                <div v-else>
                  <form @submit.prevent="changePassword">
                    <h2>비밀번호 변경</h2>
                    <input type="password" class="form-control" placeholder="새 비밀번호 입력" v-model="password">
                    <input type="password" class="form-control" placeholder="비밀번호 확인">
                    <button type="submit" class="btn btn-primary" @click="checkAuthNumber" style="width: 100%">비밀번호 변경
                    </button>
                  </form>
                </div>


                <!-- Popup for authentication number sent confirmation -->
                <div class="popup" v-if="isAuthResentPopupVisible">
                  <div class="popup-content">
                    <p>인증번호를 발송 하였습니다. 확인해주세요.</p>
                    <button @click="closeAuthResentPopup">확인</button>
                  </div>
                </div>

                <div class="popup" v-if="isAuthResentPopupVisible2">
                  <div class="popup-content">
                    <p>인증번호를 재발송 하였습니다. 확인해주세요.</p>
                    <button @click="closeAuthResentPopup2">확인</button>
                  </div>
                </div>

                <div class="popup" v-if=" inCorrectAuthNumber">
                  <div class="popup-content">
                    <p>인증번호가 틀립니다.</p>
                    <button @click="closeAuthNumberPopup">확인</button>
                  </div>
                </div>

                <div class="popup" v-if="successChangePassword">
                  <div class="popup-content">
                    <p>비밀번호가 변경되었습니다.</p>
                    <button type="button" class="btn btn-primary" data-bs-dismiss="modal" aria-label="Close">확인</button>
                  </div>
                </div>

               <div class="popup" v-if="inCorrectAuthNumberPopupVisible">
                    <div class="popup-content">
                      <p>인증번호 만료</p>
                      <p>인증시간이 만료되었습니다.</p>
                      <p>인증번호를 다시 요청해 주세요</p>
                       <button @click="closeAuthNumberPopup">확인</button>
                    </div>
                  </div>

                <!-- Popup for failed transmission -->
                <div class="popup" v-if="inCorrectMemberInfo">
                  <div class="popup-content">
                    <p>정보가 불일치합니다.</p><p>다시 확인해 주세요.</p> 
                    <button @click="closeMemberInfoPopup">확인</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </teleport>
  </div>
</template>


<script setup>
import {ref, onMounted} from 'vue';
import ApiService from "@/services/ApiService";

const id = ref('');
const tel = ref('');
const password = ref('');
const findpw = ref(true);
const authNumber = ref('');
const isPopupVisible = ref(false);
const timeLeft = ref('03:00');
let timerInstance = null; // 타이머 인스턴스를 관리하는 변수
const isAuthResentPopupVisible = ref(false);
const isAuthResentPopupVisible2 = ref(false);
// 추가: 아이디와 휴대폰번호의 readonly 상태를 나타내는 변수
const isIdReadOnly = ref(false);
const isTelReadOnly = ref(false);
const showResend = ref(false);
const randomAuthNumber = ref('');
const isAuthNumberCorrect = ref(false);
const successChangePassword = ref(false);
const inCorrectAuthNumber = ref(false);
const inCorrectMemberInfo = ref(false);
const inCorrectAuthNumberPopupVisible = ref(false); // 추가

const searchIdMember = async () => {
  try {
    const response = await ApiService.searchMembersByIdAndTel({id: id.value, tel: tel.value});
    if (response.data && response.data.value) {
      findpw.value = false;
      isPopupVisible.value = true;
      startTimer();
      isIdReadOnly.value = true;
      isTelReadOnly.value = true;
      resendAuth(response.data.value[0].id, response.data.value[0].tel);
      inCorrectMemberInfo.value = false;
    } else {
      findpw.value = null;
      isPopupVisible.value = true; // 전송 실패 시 팝업 표시
      inCorrectMemberInfo.value=true;
    }
  } catch (error) {
    console.error('Error fetching members data:', error);
    findpw.value = null;
    isAuthResentPopupVisible.value = false;
    inCorrectMemberInfo.value=true;
    
  }
};

const changePassword = async () => {
  try {
    const response = await ApiService.changePassword({id: id.value, password: password.value})
    if (response.data && response.data.success) {
      successChangePassword.value = true;
    } else {
      alert(response.data.error);
    }
  } catch (error) {
    alert(error.data.error);
  }
};



const closeAuthResentPopup = () => {
  isAuthResentPopupVisible.value = false;
};

const closeAuthResentPopup2 = () => {
  isAuthResentPopupVisible2.value = false;
};

const closeAuthNumberPopup = () => {
  inCorrectAuthNumber.value = false;
  inCorrectAuthNumberPopupVisible.value = false;
};

const closeMemberInfoPopup = () => {
  inCorrectMemberInfo.value = false;
};

const resendAuth = (responseId, responseTel) => {
  if (id.value === responseId && tel.value === responseTel) {
    isAuthResentPopupVisible.value = true;
    showResend.value = true;
  } else {
    isAuthResentPopupVisible.value = true;
  }
};

const generateAuthNumber = () => {
  randomAuthNumber.value = Math.floor(100000 + Math.random() * 900000).toString();
  console.log("인증번호 : ", randomAuthNumber.value);
};

const startTimer = () => {
  // 기존 타이머가 있으면 정지
  if (timerInstance) {
    generateAuthNumber();
    isAuthResentPopupVisible2.value = true;
    clearInterval(timerInstance);
  }else {
    generateAuthNumber();
  }

  let totalTime = 180; // 3분
  timerInstance = setInterval(() => {
    const minutes = Math.floor(totalTime / 60);
    const seconds = totalTime % 60;
    timeLeft.value = minutes.toString().padStart(2, '0') + ":" + seconds.toString().padStart(2, '0');
    totalTime -= 1;

    if (totalTime < 0) {
      clearInterval(timerInstance);
       isAuthResentPopupVisible2.value = false; // 시간 만료 팝업 닫기
      // 인증번호 만료 팝업 띄우기
      inCorrectAuthNumberPopupVisible.value = true;
    }
  }, 1000);
};

const checkAuthNumber = () => {
  if (authNumber.value === randomAuthNumber.value) {
    isAuthNumberCorrect.value = true;
    inCorrectAuthNumber.value = false;
  } else {
    isAuthNumberCorrect.value = false;
    inCorrectAuthNumber.value = true;
  }
};

onMounted(() => {
  const modalElement = document.getElementById('findPasswordModal');
  modalElement.addEventListener('hidden.bs.modal', () => {
    id.value = '';
    tel.value = '';
    password.value = '';
    authNumber.value = '';
    findpw.value = true;
    isAuthNumberCorrect.value = false;
    isAuthResentPopupVisible.value = false;
    isAuthResentPopupVisible2.value = false;
    showResend.value = false;
    isIdReadOnly.value = false;
    isTelReadOnly.value = false;
    successChangePassword.value = false;

    // Reset the timer if it's running
    if (timerInstance) {
      clearInterval(timerInstance);
      timerInstance = null;
      timeLeft.value = '03:00';
    }
  });
});
</script>

<style>
.popup {
  position: fixed;
  /* 고정 위치 */
  left: 50%;
  /* 화면 가운데 정렬 */
  top: 20%;
  /* 화면 가운데 정렬 */
  transform: translate(-50%, -50%);
  /* 화면 가운데 정렬 */
  z-index: 1000;
  /* 다른 요소들 위에 표시 */
  background-color: white;
  /* 배경색 */
  border: 1px solid #ddd;
  /* 테두리 스타일 */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  /* 그림자 효과 */
  padding: 20px;
  /* 내부 여백 */
  width: 450px;
  /* 팝업 너비 */
  display: flex;
  /* 플렉스 레이아웃 */
  flex-direction: column;
  /* 세로 방향 정렬 */
  align-items: center;
  /* 가운데 정렬 */
}

.popup-content {
  margin-bottom: 20px;
}

/* 필요한 경우 추가 버튼 스타일 */
.popup button {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.popup button:hover {
  background-color: #0056b3;
}
</style>