import axios from 'axios';

const API_URL = '/';
export default {
    login(loginData) {
        return axios.post(API_URL + "login", loginData);
    },
    searchMembersByNameAndTel(searchData) {
        return axios.post(API_URL + "searchMemberId", searchData);
    },
    searchMembersByIdAndTel(searchData) {
        return axios.post(API_URL + "searchMemberPw", searchData);
    },
    changePassword(passwordData) {
        return axios.post(API_URL + "changePassword", passwordData);
    },
    serviceHistory(searchData) {
        return axios.get(API_URL + "serviceHistoryList", {
            params: searchData
        });
    }
};
