<template>
  <teleport to="body">
    <div class="modal fade" id="findMemberIdModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h1 class="modal-title fs-5" id="exampleModalLabel">아이디 찾기</h1>
            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
          </div>
          <div class="modal-body">
            <div v-if="findid === true && members.length === 0">
              <form @submit.prevent="searchIdMember" class="card p-4">
                <div class="mb-3">
                  <label for="searchName" class="form-label">이름</label>
                  <input type="text" id="searchName" class="form-control" v-model="userName">
                </div>
                <div class="mb-3">
                  <label for="searchTel" class="form-label">휴대폰번호</label>
                  <input type="text" id="searchTel" class="form-control" v-model="tel">
                </div>
                <button type="submit" class="btn btn-primary">아이디 찾기</button>
              </form>
            </div>
            <div v-else-if="members.length > 0">
              <div v-for="member in members" :key="member.id">
                아이디 : {{ member.id }}<br/>
                가입일 : {{ member.registerDate }}<br/><br/>
              </div>
              <button type="submit" class="btn btn-primary" data-bs-dismiss="modal" aria-label="Close"
                      style="width: 100%;">로그인하기
              </button>
            </div>
            <div class="modal fade" id="infoMismatchModal" tabindex="-2" aria-labelledby="infoMismatchModalLabel" aria-hidden="true"
            style="margin-top: 100px;">
              <div class="modal-dialog">
                <div class="modal-content">
                  <div class="modal-body">
                    <p>정보조회 불일치</p>
                   <p> 해당하는 ID가 없습니다.</p>
                  </div>
                  <div class="modal-footer">
                    <button type="button" class="btn btn-primary" data-bs-dismiss="modal" style="width: 100%;">확인</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </teleport>

</template>


<script setup>
import {ref, onMounted} from 'vue';
import ApiService from "@/services/ApiService";
import moment from 'moment';

const userName = ref('');
const tel = ref('');
const members = ref([]);
const findid = ref(true);
const bootstrap = require('bootstrap');

function formatDate(inputDate) {
  return moment(inputDate).format('YYYY-MM-DD');
}

const searchIdMember = async () => {
  try {
    const response = await ApiService.searchMembersByNameAndTel({userName: userName.value, tel: tel.value});
    if (response.data && response.data.value) {
      members.value = response.data.value.map(member => ({
        ...member,
        registerDate: formatDate(member.registerDate)
      }));
      findid.value = false;
    } else {
      findid.value = true;
      new bootstrap.Modal(document.getElementById('infoMismatchModal')).show();
    }
  } catch (error) {
    console.error('Error fetching members data:', error);
    findid.value = true;
    new bootstrap.Modal(document.getElementById('infoMismatchModal')).show();
  }
};


onMounted(() => {
  const modalElement = document.getElementById('findMemberIdModal');
  modalElement.addEventListener('hidden.bs.modal', () => {
    findid.value = true;
    userName.value = '';
    tel.value = '';
    members.value = [];
  });
});
</script>
