<template>
  <div class="container mt-5">
    <form @submit.prevent="login" class="card p-4">
      <div class="mb-3">
        <label for="id" class="form-label">아이디</label>
        <input type="text" id="id" class="form-control" v-model="memberDTO.id">
      </div>
      <div class="mb-3">
        <label for="password" class="form-label">비밀번호</label>
        <input type="password" id="password" class="form-control" v-model="memberDTO.password">
      </div>
      <button type="submit" class="btn btn-primary">로그인</button>
    </form>
  </div>
  <!-- 아이디 찾기 버튼 -->
  <button type="button" class="btn btn-link" data-bs-toggle="modal" data-bs-target="#findMemberIdModal">
    아이디 찾기
  </button>
  <!-- 비밀번호 찾기 버튼 -->
  <button type="button" class="btn btn-link" data-bs-toggle="modal" data-bs-target="#findPasswordModal">
    비밀번호 찾기
  </button>

  <!-- FindMemberIdComponent 모달 추가 -->
  <FindMemberIdComponent />
  <FindPasswordComponent />
</template>

<script>
import ApiService from "@/services/ApiService";
import { useUserStore } from '@/store/UserStore';
import FindMemberIdComponent from '@/components/FindMemberIdComponent.vue';
import FindPasswordComponent from '@/components/FindPasswordComponent.vue';
export default {
  name: "LoginComponent",
  components : {FindMemberIdComponent,FindPasswordComponent },
  data() {
    return {
      memberDTO: { id: "", password: "" },
    };
  },
  methods: {
    async login() {
      if (!this.memberDTO.id || !this.memberDTO.password) {
        alert('아이디와 비밀번호를 입력해주세요');
        return;
      }

      try {
        const response = await ApiService.login(this.memberDTO);
        alert('로그인에 성공하였습니다.');
        console.log(response.data.value);

        const userStore = useUserStore();
        userStore.setUser(response.data.value);
         // 로그인 성공 후 /service 페이지로 이동
        this.$router.push('/service');
      } catch (error) {
        alert(error.response.data.error);
      }
    }
  }
};

</script>

