<template>
  <div>
    <br /><br /><br /><br /><br /><br /><br />
    <button class="btn" @click="toggleMasking"><font-awesome-icon :icon="['fas', 'list']" /></button>
    <br /><br />
    <p v-if="userInfo">이름: {{ maskedName }}</p>
    <p v-if="userInfo">전화번호: {{ maskedTel }}</p>
    <p v-else>사용자 정보가 없습니다.</p>
  </div>
</template>

<script>
import { useUserStore } from '@/store/UserStore';

export default {
  data() {
    return {
      isMasked: true,
    };
  },
  computed: {
    userInfo() {
      const userStore = useUserStore();
      return userStore.user;
    },
    maskedName() {
      if (!this.userInfo || !this.userInfo.userName) return '';

      // 마스킹이 활성화되어 있다면
      if (this.isMasked) {
        // 이름의 처음 두 글자를 가져옴
        const firstTwoChars = this.userInfo.userName.substring(0, 2);

        // 나머지 부분은 '*'로 마스킹
        const maskedPart = '*'.repeat(this.userInfo.userName.length - 1);

        // 처음 두 글자와 마스킹된 부분을 합쳐서 반환
        return firstTwoChars + maskedPart;
      }

      // 마스킹이 비활성화되어 있다면 원본 이름 반환
      return this.userInfo.userName;
    },
    maskedTel() {
      // 사용자 정보가 없거나 전화번호가 없으면 빈 문자열 반환
      if (!this.userInfo || !this.userInfo.tel) return '';

      // 마스킹이 활성화되어 있다면
      if (this.isMasked) {
        // 전화번호를 '-'를 기준으로 나눔
        const parts = this.userInfo.tel.split('-');

        // 전화번호가 세 부분으로 나누어져 있다면 (예: 010-1234-5678)
        if (parts.length === 3) {
          // 두 번째 부분을 '*'로 마스킹
          const maskedSecondPart = '*'.repeat(parts[1].length - 2);

          // 세 번째 부분에서 앞 한 자리만 '*'로 마스킹
          const maskedThirdPart = '*' + parts[2].substring(1);

          // 마스킹된 두 번째 부분과 세 번째 부분을 포함한 전체 전화번호를 반환
          return `${parts[0]}-${maskedSecondPart}-${maskedThirdPart}`;
        }
      }

      // 마스킹이 비활성화되어 있다면 원본 전화번호 반환
      return this.userInfo.tel;
    },

  },
  methods: {
    toggleMasking() {
      this.isMasked = !this.isMasked;
    },
  },
};
</script>

