<template>
  <div class="service-history-container">
    <div style="margin: 50px; text-align: right">
      <!-- Toggle Button for Search Period Section -->
      <button class="btn" @click="toggleSearchPeriodVisibility">
          {{ getDefaultSearchInfo() }}
        <font-awesome-icon :icon="['fas', 'list']"/>       
      </button>
    </div>

    <!-- 서비스 내역 표시 -->
    <div v-if="serviceHistory.length">
      <div style="width: 80%;  margin: 60px auto 0 auto">
        <table class="table">
          <thead>
          <tr>
            <th>상담일</th>
            <th>상담사</th>
            <th>삼당 상태</th>
            <th>상담 유형</th>
            <th>상담 결과</th>
            <th>다음 상담일</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="service in serviceHistory" :key="service.sno">
            <td>{{ formatDate(service.counselingDate, 'YYYY-MM-DD') }}</td>
            <td>{{ service.counselingName }}</td>
            <td>{{ service.counselingState ? '진행' : '완료' }}</td>
            <td>{{ service.counselingCategory }}</td>
            <td>{{ service.counselingResult }}</td>
            <td>{{ formatDate(service.counselingNextDay, 'MM.DD(dd)') }}</td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div v-else>
      <p>검색된 상담 내역이 없습니다.</p>
    </div>
  </div>

  <!-- Search Period Popup -->
  <div class="popup" v-if="isSearchPeriodVisible">
    <div class="popup-content">
      <div>
        <button type="button" @click="toggleSearchPeriodVisibility" class="btn-close"
                style="position: relative; right: 0px;"></button>
      </div>
      <div class="date-shortcuts">
        <button class="btn btn-light" type="button" @click="setDateRange(7)">1주일</button>
        <button class="btn btn-light" type="button" @click="setDateRange(30)">1개월</button>
        <button class="btn btn-light" type="button" @click="setDateRange(90)">3개월</button>
      </div>
      <form @submit.prevent="fetchServiceHistory">
        <div>
          <input class="form-control" type="date" v-model="searchData.startDate"/>
          <input class="form-control" type="date" v-model="searchData.endDate"/>
        </div>
        <div class="sort-buttons">
          <button class="btn btn-primary" type="button" @click="sortServiceHistory('newest')">최신순</button>
          <button class="btn btn-primary" type="button" @click="sortServiceHistory('oldest')">과거순</button>
        </div>
        <div class="popup-buttons">
          <button class="btn btn-light" type="button" @click="resetSearchData">초기화</button>
          <button class="btn btn-primary" type="submit">완료</button>
        </div>
      </form>
    </div>
  </div>

</template>

<script>
import ApiService from "@/services/ApiService";
import { useUserStore } from "@/store/UserStore";
import moment from "moment/moment";

export default {
  name: "ServiceComponent",
  data() {
    return {
      searchData: {
        startDate: '',
        endDate: ''
      },
      serviceHistory: [],
      isSearchPeriodVisible: false
    };
  },
  computed: {
    userId() {
      const userStore = useUserStore();
      return userStore.user.id;
    }
  },
  mounted() {
    // 기본 검색 기간을 1달로 설정
    this.setDateRange(30);
    this.fetchServiceHistory();
  },
  methods: {
    async fetchServiceHistory() {
      const params = {
        id: this.userId,
        startDate: this.searchData.startDate,
        endDate: this.searchData.endDate
      };

      try {
        const response = await ApiService.serviceHistory(params);
        if (response.data && response.data.value) {
          this.serviceHistory = response.data.value;
        } else {
          this.serviceHistory = [];
          alert("검색된 서비스 내역이 없습니다.");
        }
      } catch (error) {
        alert(error.response.data.error || "데이터를 가져오는 중 문제가 발생했습니다.");
        this.serviceHistory = [];
      }
    },
    setDateRange(days) {
      const endDate = new Date();
      const startDate = new Date();
      startDate.setDate(endDate.getDate() - days);

      this.searchData.startDate = startDate.toISOString().split('T')[0];
      this.searchData.endDate = endDate.toISOString().split('T')[0];
    },
    sortServiceHistory(order) {
      if (order === 'newest') {
        this.serviceHistory.sort((a, b) => new Date(b.counselingDate) - new Date(a.counselingDate));
      } else {
        this.serviceHistory.sort((a, b) => new Date(a.counselingDate) - new Date(b.counselingDate));
      }
    },
    resetSearchData() {
      this.searchData.startDate = '';
      this.searchData.endDate = '';
    },
    toggleSearchPeriodVisibility() {
      this.isSearchPeriodVisible = !this.isSearchPeriodVisible;
    },
    formatDate(date, formatType) {
      const formattedDate = moment(date).format(formatType);
      if (formatType === 'MM.DD(dd)') {
        return formattedDate.replace('Mo', '월')
          .replace('Tu', '화')
          .replace('We', '수')
          .replace('Th', '목')
          .replace('Fr', '금')
          .replace('Sa', '토')
          .replace('Su', '일');
      }
      return formattedDate;
    },
    getDefaultSearchInfo() {
      // 새로운 메서드의 내용 추가
      const startDateFormatted = this.formatDate(this.searchData.startDate, 'YYYY-MM-DD');
      const endDateFormatted = this.formatDate(this.searchData.endDate, 'YYYY-MM-DD');
      const dateInfo = `${startDateFormatted} ~ ${endDateFormatted}`;
      const orderInfo = this.isOrderNewest ? '최신순' : '최신순';

      return `(${dateInfo}, ${orderInfo})`;
    }
  }
};
</script>




<style>
.popup {
  position: fixed; /* 고정 위치 */
  left: 50%; /* 화면 가운데 정렬 */
  top: 40%; /* 화면 가운데 정렬 */
  z-index: 1000; /* 다른 요소들 위에 표시 */
  background-color: white; /* 배경색 */
  border: 1px solid #ddd; /* 테두리 스타일 */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* 그림자 효과 */
  padding: 20px; /* 내부 여백 */
  width: 450px; /* 팝업 너비 */
  display: flex; /* 플렉스 레이아웃 */
  flex-direction: column; /* 세로 방향 정렬 */
}

.popup-content {
  margin-bottom: 20px;
}

.popup button {
  margin: 10px;
}

</style>