<template>
  <nav class="navbar navbar-expand-lg navbar-light bg-light">
    <div class="container-fluid">
      <div class="collapse navbar-collapse">
        <ul class="navbar-nav ms-auto">
          <li class="nav-item">
            <router-link class="nav-link" to="/service">서비스 내역</router-link>
          </li>
          <li class="nav-item">
            <router-link class="nav-link" to="/memberInfo">내 정보</router-link>
          </li>
          <li class="nav-item">
            <button v-if="isLoggedIn" class="nav-link" @click="showLogoutModal">로그아웃</button>
            <router-link v-else class="nav-link" to="/">로그인</router-link>
          </li>
        </ul>
      </div>
    </div>

    <!-- 로그아웃 확인 팝업 -->
    <div v-if="isLogoutModalVisible" class="logout-modal">
      <div class="logout-modal-content">
        <p>로그아웃 하시겠습니까?</p>
        <button @click="confirmLogout">확인</button>
        <button @click="hideLogoutModal">취소</button>
      </div>
    </div>
  </nav>
</template>

<script>
import { useUserStore } from "@/store/UserStore";
import { computed, ref } from 'vue';
import { useRouter } from 'vue-router';

export default {
  name: 'MainHeader',
  setup() {
    const userStore = useUserStore();
    const isLoggedIn = computed(() => userStore.user !== null);
    const router = useRouter();
    const isLogoutModalVisible = ref(false);

    const showLogoutModal = () => {
      isLogoutModalVisible.value = true;
    };

    const confirmLogout = () => {
      userStore.setUser(null);
      router.push('/');
      hideLogoutModal();
    };

    const hideLogoutModal = () => {
      isLogoutModalVisible.value = false;
    };

    return { isLoggedIn, showLogoutModal, isLogoutModalVisible, confirmLogout, hideLogoutModal };
  }
};
</script>

<style scoped>
.logout-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.logout-modal-content {
  background: white;
  padding: 20px;
  border-radius: 8px;
  text-align: center;
}
</style>
